import React from 'react';
import { Modal, Carousel } from 'antd';

const GameModal = ({ game, isVisible, onClose }) => {
  if (!game) return null; 
  return (
    <Modal
      title={game.title}
      open={isVisible}
      onCancel={onClose}
      footer={null}
    >
      
      <Carousel autoplay>
  <div>
    <img alt="game image" src={game.images[0]} style={{ width: '100%', height: 'auto' }} />
  </div>
</Carousel>
      <p>{game.description}</p>
    </Modal>
  );
};

export default GameModal;
